import { AbstractFile } from "@observablehq/stdlib";
import { importParser } from "./MarkedTemplateParser";
export const load = async (name) => {
    const fetchResponse = await fetch(name);
    const text = await fetchResponse.text();
    return text;
};
export const importContent = (content, module) => importParser(content, module);
class FA extends AbstractFile {
    constructor(name) {
        super(name, name);
    }
    url() {
        return this.name;
    }
}
export const loadSource = (url) => new FA(url);
